import './ContactUs.styl';

import React, {
  Component,
} from 'react';

import {
  Header,
  HeaderBottom,
  ContactContent
} from 'components';

import PropType from 'prop-types';

export default class ContactUs extends Component {
  static propTypes = {
    match: PropType.object
  };

  render() {
    const path = this.props.match.path;

    return (
      <div className="c-contact-us">
        <div className="contact-us-container">
          <Header
            indexPage={3}
          />
          <HeaderBottom
            oneBlock={true}
            window={true}
            nav={true}
            path={path}
          />
          <div className="contact-us-body-container">
            <ContactContent />
          </div>
        </div>
      </div>
    );
  }
}



